.social{
    background-color:transparent;
    position: absolute;
    padding: 5px;
    top: 95%;
    left:50%;
    transform: translate(-50%, -90%);
    display: flex;
    align-items: center;
    justify-content: center;
    justify-content: space-between;
    width: 60%;
    border-top: 1px solid $color-text;
    border-bottom: 1px solid $color-text;
    .copyright{
        p{
            color: $color-text;
        }
    }
    .icon-list{
        width: 30%;
        display: flex;
        justify-content: space-between;
        padding: 2px;
        
        a{
            font-size: 1.5rem;
            color: $color-1;
            transition: 0.2s;
            &:hover{
                color: $white;
                transform: scale(1.5);
            }
        }
    }
   
    
}

@media screen and (max-width: 1100px){
    .social{
        width: 80%;
        .copyright{
            p{
                color: $color-text;
                font-size: 0.8rem;
            }
        }
        .icon-list{
            width: 30%;
            display: flex;
            justify-content: space-between;
            padding: 2px;
            
            a{
                font-size: 1rem;
                color: $color-1;
                transition: 0.2s;
                &:hover{
                    color: $white;
                    transform: scale(1.5);
                }
            }
        }
       
        
    }
}

@media screen and (max-width: 830px){
    .social{
        width: 80%;
        top: 99%;
        flex-direction: column-reverse;
        .copyright{
            text-align: center;
            
            p{
                color: $color-text;
                font-size: 0.8rem;
            }
        }
        .icon-list{
            width: 50%;
            margin-bottom: 0.5rem;
            a{
                
              
            }
        }
       
        
    }
}

